.profile__info {
  grid-area: info;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 16px;
}

@media all and (max-width: 730px) {
  .profile__info  {
    gap: 5px;
  }
}

@media all and (max-width: 384px) {
  .profile__info  {
    margin: 26px 0 36px 0;
    justify-content: center;
    flex-direction: column;
    gap: 8px;
  }
}
